<template>
  <div id="app">
    <div>
      <label class="switch">
        <input type="checkbox" checked />
        <span class="slider round" @click="toogleTheme()"></span>
      </label>
    </div>

    <Nav></Nav>

    <transition name="fade" mode="out-in">
      <router-view />
    </transition>

    <div></div>
    <Footer></Footer>
  </div>
</template>

<script>
import Nav from "./components/NavV.vue";
import Footer from "./components/Footer.vue";

export default {
  name: "App",
  components: {
    Nav,
    Footer,

    //Header
  },
  methods: {
    toogleTheme() {
      document.body.classList.toggle("dark-mode");
      document.querySelector(".c-tile").classList.toggle("invert");
      document.querySelector(".c-tile-a").classList.toggle("invert");
      document.querySelector(".c-tile-f").classList.toggle("invert");
    },
  },
  mounted() {
    document.body.classList.add("bg-app");
    document.body.classList.add("no-select");
  },
  destroyed() {
    document.body.classList.remove("bg-app");
  },
};
</script>

<style>
.no-select {
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none;          /* Likely future */      
}

.switch {
  position: fixed;
  width: 60px;
  height: 34px;
  left: 90%;
  top: 3%;
  z-index: 200;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.bg-app {
  margin: 0;
  background: linear-gradient(-52deg, #ee7752, #e73c7e, #23a6d5, #0e5d71);
}

.dark-mode {
  background: hsl(218, 28%, 13%);
}
.invert {
  filter: invert(1) !important;
}
.invert:hover {
  filter: none !important;
}

.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}
@media (max-width: 768px) {
  .switch {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 1.7%;
    left: 80%;
  }
}
</style>
